import { Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { MailTemplate } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { Box, MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";

export default function ManageTemplateMailModifyComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [code, setCode] = useState<string>('');
    const [title, setTitle] = useState<string>('');
    const [content, setContent] = useState<string>('');
    const keyData: string | undefined = useParams().key;

    const nav = useNavigate();
    const loadData = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            MailTemplate.GetDetail(keyData)
                .then((res: any) => {
                    if (res.isSuccess) {
                        setCode(res.data.code);
                        setTitle(res.data.title);
                        setContent(res.data.content);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyData]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleSubmitForm() {
        const body = {
            title: title,
            code: code,
            content: content
        };

        setIsLoading(true);
        if (keyData === undefined) {
            add(body);
        } else {
            edit(body);
        }
    }

    function add(body: any) {
        MailTemplate.Add(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
            .finally(() => setIsLoading(false));
    }

    function edit(body: any) {
        MailTemplate.Detail(keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    MailTemplate.Edit(keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                            }
                        })
                        .finally(() => setIsLoading(false));
                }
                else setIsLoading(false)
            });
    }


    function onClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa template này không?"
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                Delete();
            }
        })
    }

    function Delete() {
        MailTemplate.Detail(keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    MailTemplate.Delete(keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                nav("/admin/template");
                            }
                        })
                        .finally(() => setIsLoading(false))

                }
                else setIsLoading(false)
            })
    }

    return (
        <MyBox
            isLoading={isLoading}
            title={keyData === undefined ? 'Thêm template' : 'Chỉnh sửa tenplate'} >
            <Box>
                <Form 
                    layout="vertical"
                    onSubmitCapture={handleSubmitForm}>
                    <MyFormItem.Input
                        label="Code"
                        value={code}
                        setValue={setCode} />
                    <MyFormItem.Input
                        label="Tiêu đề"
                        value={title}
                        setValue={setTitle} />
                    <MyFormItem.TextArea
                        label="Nội dung"
                        value={content}
                        setValue={setContent} />
                    <div dangerouslySetInnerHTML={{ __html: content }} className="preview">
                    </div>
                    <hr />
                    <MyButton
                        value={keyData === undefined ? 'Thêm' : 'Chỉnh sửa'}
                        htmlType="submit"
                        type="success" />

                    <MyButton
                        type="danger"
                        value="Xóa"
                        onClick={onClickDelete} />
                </Form>
            </Box>
        </MyBox>
    );
}